import React from 'react'
import { Link } from 'react-router-dom'

const Navbar = (props) => {
  return (
    <div>
        <nav className={`navbar navbar-expand-lg navbar-${props.mode} bg-${props.mode}`}>
          <div className="container-fluid">
    <Link className="navbar-brand" to="/">Home</Link>
    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
      <span className="navbar-toggler-icon"></span>
    </button>
    <div className="collapse navbar-collapse" id="navbarSupportedContent">
      <ul className="navbar-nav me-auto mb-2 mb-lg-0">
        <li className="nav-item">
          <Link className="nav-link active" aria-current="page" to="/bmicalculator">BMI-Calculator</Link>
        </li>
        <li className="nav-item">
          <Link className="nav-link active" aria-current="page" to="/calculator">Calculator</Link>
        </li>
        <li className="nav-item">
          <Link className="nav-link active" aria-current="page" to="/todolist">To-Do-List</Link>
        </li>
        <li className="nav-item">
          <Link className="nav-link active" aria-current="page" to="/notes">Notes</Link>
        </li>
        <li className="nav-item">
          <Link className="nav-link active" aria-current="page" to="/textutil">Text-Utility</Link>
        </li>
        <li className="nav-item">
          <Link className="nav-link active" aria-current="page" to="/timer">Timer</Link>
        </li>
        

      </ul>
    <div className={`form-check form-switch text-${props.mode === 'light' ? 'dark' : 'light'}`}>
        <input className="form-check-input" onClick = {props.toggleMode} type="checkbox" role="switch" id="flexSwitchCheckDefault"/>
        <label className="form-check-label" htmlFor="flexSwitchCheckDefault">Dark Mode</label>
    </div>
    </div>
  </div>
</nav>
    </div>
  )
}

export default Navbar