import React from 'react'
import { useFormik } from 'formik'
import formSchema from '../Schema/Schemas.js'
import '../App.css'

const HomePage = () => {

  const onSubmit = async (values, action) => {
    console.log(values)
    console.log(action)
    await new Promise(resolve => setTimeout(resolve, 1500))
    action.resetForm()
  }

  const { values, errors, touched, handleChange, handleSubmit, isSubmitting } = useFormik({
    initialValues: {
      userName:'',
      email:'',
      password: '',
      confirmPassword: ''
    },
    validationSchema: formSchema,
    onSubmit
  })

  console.log(errors)
  
  

  return (
    <form onSubmit={handleSubmit} autoComplete="off">
      <div className="text-center">
      <h3>Registration Form</h3>
      </div>
      <div className="container" style={{ maxWidth: '400px' }}>
        <div className="mb-3">
          <label htmlFor="userName" className="form-label">
            User Name
          </label>
          <input
            id="userName"
            name="userName"
            placeholder= {errors.email && touched.email ? "User Name should not be empty" : "Enter UserName"}
            className={`form-control ${errors.userName && touched.userName ? "input-error" : ""}`}
            value={values.userName}
            onChange={handleChange}
            
          />
        </div>
        <div className="mb-3">
          <label htmlFor="email" className="form-label">
            Email
          </label>
          <input
            id="email"
            name="email"
            placeholder= {errors.email && touched.email ? "Email should not be empty" : "Enter Email"}
            className={`form-control ${errors.email && touched.email ? "input-error" : ""}`}
            value={values.email}
            onChange={handleChange}
          />
        </div>
        <div className="mb-3">
          <label htmlFor="password" className="form-label">
            Password
          </label>
          <input
            id="password"
            name="password"
            placeholder="Create Password"
            type="password"
            className={`form-control ${errors.password && touched.password ? "input-error" : ""}`}
            value={values.password}
            onChange={handleChange}
          />
          {errors.password && touched.password && <p>{errors.password}</p>}
        </div>
        <div className="mb-3">
          <label htmlFor="confirmPassword" className="form-label">
            Confirm Password
          </label>
          <input
            id="confirmPassword"
            name="confirmPassword"
            placeholder="Confirm your Password"
            type="password"
            className={`form-control ${errors.confirmPassword && touched.confirmPassword ? "input-error" : ""}`}
            value={values.confirmPassword}
            onChange={handleChange}
          />
          {errors.confirmPassword && touched.confirmPassword && <p>{errors.confirmPassword}</p>}
        </div>
        <button disabled = {isSubmitting} type="submit" className="btn btn-primary">
          Submit
        </button>
      </div>
    </form>
  )
}

export default HomePage